import React, { useRef, useState } from 'react'
import { Card, Form, Button, Alert } from 'react-bootstrap'
import { useAuth } from '../context/AuthContext'
import { Link, useHistory } from 'react-router-dom'

export default function Signin() {
const emailRef               = useRef()
const passwordRef            = useRef()
const { signin }             = useAuth()
const [error, setError]      = useState('')
const [loading, setLoading]  = useState(false)
const history                = useHistory()

async function handleSubmit(e){
    e.preventDefault()

    try {
    setError('') // set Error back to an empty string
    setLoading(true)
    await signin(emailRef.current.value, passwordRef.current.value)
    history.push('/profile')
    } catch {
        setError('Sign in Error')
    }
    setLoading(false)
}

return (
    <>
    <Card>
        <Card.Body>
            <h1 className='text-center mb-4'>Sign In</h1>
            {error && <Alert varient="danger">{error}</Alert>}
            <Form onSubmit={handleSubmit}>
                <Form.Group id='email'>
                    <Form.Label>EMAIL</Form.Label>
                    <Form.Control type='email' ref={emailRef} required />
                </Form.Group>
                <Form.Group id='password'>
                    <Form.Label>PASSWORD</Form.Label>
                    <Form.Control type='password' ref={passwordRef} required />
                </Form.Group>
                <Button disabled={loading} type='submit' className='w-100'>Sign in</Button>
            </Form>
        </Card.Body>
    </Card>
    <div className='w-200 text center mt-2'>
        create new account? <Link to ='/signup'>HERE</Link>
    </div>
    </>
)
}

