import axios from 'axios';
import React, {useState, useEffect} from 'react';
import { useAuth } from '../context/AuthContext'

function AllData(){
    const [userAll, setUserAll] = useState([]);    
    const [changedPassword, setChangePassword] = useState('');
    const [changedAmount, setChangeAmount]     = useState(0);


    useEffect(() => {
        
        
        // fetch all accounts from API
        // fetch('http://localhost:3001/allData')
        //     .then(response => response.json())
        //     .then(data => {
        //         console.log(data);
        //         setData(JSON.stringify(data));                
        //     });

       axios.get('http://localhost:3001/allData').then((response)=>{
           setUserAll(response.data);
       });
    }, []);
       
    const changeAccountInfo = (id) =>{
        axios.put('http://localhost:3001/update', {
            id: id, 
            changedPassword: changedPassword,
        });
    };

    const changeAmountInfo = (id) =>{
        axios.put('http://localhost:3001/updateAmount', {
            id: id, 
            changedAmount: changedAmount,
        });
    };

    const deleteAccount = (id) =>{
        axios.delete(`http://localhost:3001/delete/${id}`, {
        });
    };


    return (
    <>
    <h1>List of Account</h1>
    <div>
            {userAll.map((value, key) => {
                return <div key ={key}> 
                <h3>{value.name}</h3>
                <p>{value.email}</p>
                <p>{value.password}</p>
                <p>{value.amount}</p>
                <input type='text' placeholder='type here...' onChange={((e)=>{setChangePassword(e.target.value)})}/>
                <button onClick={()=> changeAccountInfo(value._id)}>change</button>
                <input type='number' placeholder='funding...' onChange={((e)=>{setChangeAmount(e.target.value)})}/>
                <button onClick={()=> changeAmountInfo(value._id)}>adjust fund</button>
                <button onClick={()=> deleteAccount(value._id)}>close account</button>
                </div>  
                     
            })}
    </div>
    </>
    );
};

export default AllData;