import React, { useRef, useState, useEffect, useContext } from 'react'
import { Card, Form, Button, Alert } from 'react-bootstrap'
import { useAuth } from '../context/AuthContext'
import { UserContext } from '../context'
import { Link, useHistory } from 'react-router-dom'
import axios from 'axios'
import { updateCurrentUser } from '@firebase/auth'


export default function Profile() {

const [userAll, setUserAll] = useState([]);    
const [userA, setUserA] = useState([]);    
const [changedPassword, setChangePassword] = useState('');
const [changedAmount, setChangeAmount]     = useState(0);
const { currentUser, signout }        = useAuth()
const [error, setError]      = useState('');
const history                = useHistory()

useEffect(() => {
    
axios.get(`http://localhost:3001/allData`).then((response)=>{
       setUserAll(response.data);
   });
}, []);
   
const changeAccountInfo = (id) =>{
    axios.put('http://localhost:3001/update', {
        id: id, 
        changedPassword: changedPassword,
    });
};

const changeAmountInfo = (id) =>{
    axios.put('http://localhost:3001/updateAmount', {
        id: id, 
        changedAmount: changedAmount,
    });
};

const deleteAccount = (id) =>{
    axios.delete(`http://localhost:3001/delete/${id}`, {
    });
};

async function handleSignout(){
    setError('')

    try {
        await signout()
        history.push('/signin')
    } catch {
        setError('Signout Error')
    }
}



const getUser = () =>{
    const URL = 'http://localhost:3001/'+ currentUser.email
    console.log('http://localhost:3001/'+ currentUser.email)
    axios.get(URL, {
    })
    .then((response)=>{
    setUserA(response.data);
    });;
    console.log(JSON.stringify(userA))
};

return (
<>
<Card>
<h1>Welcome: {currentUser.email}</h1>
<div>
    {/* <button className="btn btn-outline-warning" onClick={getUser}>check</button> */}
    <Form onSubmit={handleSignout}>
    <Button  type='submit' className='w-100'>Sign out</Button>
    </Form>
    <Form>
        {userAll.map((value, key) => {
            return <div key ={key}> 
            <h3>name: {value.name}</h3>
            <p>balance: {value.amount}</p>
            </div>        
        })}
    </Form>
    <h5 className="text-center">Withdraw:<a className="text-warning" href="#/Withdraw/"> here </a></h5>
    <h5 className="text-center">Deposit:<a className="text-warning" href="#/Deposit/"> here </a></h5>
    {/* <Form>
        {userAll.map((value, key) => {
            return <div key ={key}> 
            <input type='number' placeholder='add deposit here' onChange={((e)=>{setChangeAmount(e.target.value)})}/>
            <button class="btn btn-outline-warning"onClick={()=> changeAmountInfo(value._id)}>change</button>
            </div>        
         })}
    </Form>

    <Form>
         {userAll.map((value, key) => {
            return <div key ={key}> 
            <button class="btn btn-outline-warning" onClick={()=> deleteAccount(value._id)}>close account</button>
            </div>        
        })}
    </Form> */}
</div>
</Card>
</>
);
}
