import React, { useState,useEffect } from 'react'
import { Card } from 'react-bootstrap';
import { useAuth } from '../context/AuthContext';
import { Form, Button, Alert } from 'react-bootstrap'
import axios from 'axios';
import { Link, useHistory } from 'react-router-dom'

export default function Withdraw(){
    const { signout, currentUser }= useAuth()
    const [userAll, setUserAll] = useState([]);    
    const [userA, setUserA] = useState([]);    
    const [changedBalance, setChangeBalance] = useState(0);
    const [changedAmount, setChangeAmount]     = useState(0);
    const [error, setError]      = useState('');
    const history                = useHistory()

    useEffect(() => {
    
      axios.get(`http://localhost:3001/allData`).then((response)=>{
             setUserAll(response.data);
         });
      }, []);

    const changeAmountInfo = (id) =>{
      axios.put('http://localhost:3001/updateAmount', {
          id: id, 
          changedAmount: changedAmount,
      });
      history.push('/profile')
      
    };
  
    return(<>
      {currentUser.email}
      <Card>
        <Form>
            {userAll.map((value, key) => {
                return <div key ={key}> 
                <p>name: {value.name}</p>
                <h3>balance: {value.amount}</h3>
                </div>        
            })}
        </Form>
        <Form>
            {userAll.map((value, key) => {
                return <div key ={key}> 
                <input type='number' placeholder='add deposit here' onChange={((e)=>{setChangeAmount(e.target.value)})}/>
                <button class="btn btn-outline-warning"onClick={()=> changeAmountInfo(value._id)}>withdraw</button>
                </div>        
            })}
        </Form>
      </Card>

    </>);
}